// JavaScript

Template.MeteorToys_shell_header.events({
	'click #MeteoToys_shell_run': function (e, t) {
		
		e.preventDefault();
		
		var execute = String($("#MeteorToys_shell_input").val());
		
		if (execute == false) {
			alert("Please enter code to run");
			return false;
		}

		Meteor.call("MeteorToys_Shell", execute, function (e,r) {
			if (e) {
				
				alert("There was an error.");
				console.log("=====================================");
				console.log("Shell Error");
				console.log(e);
				console.log("=====================================");

			} else {
				console.log("=====================================");
				console.log("Meteor Toys ran the following method:");
				console.log("Method = function () { \n" + execute + "\n}");
				console.log("The results are");
				console.log(r);
				console.log("=====================================");
				$("#MeteorToys_shell_input").val("");

				Package['meteortoys:result'].MeteorToys_Result.insert(r, "meteortoysshell", execute);
				
			}
		});

	},
	'click #MeteoToys_shell_clear': function () {
		$("#MeteorToys_shell_input").val('');
		MeteorToys.set("Shell_Clearable", false);
		localStorage.removeItem("MeteorToys_shell");
		$("#MeteorToys_shell_input").focus();
	}
});

Template.MeteorToys_shell_header.helpers({
	clearable: function () {
		return MeteorToys.get("Shell_Clearable");
	}
});

Template.MeteorToys_shell_input.events({
	'keyup': function () {
		// console.log('ok');
		value = String($("#MeteorToys_shell_input").val()) || "";
		localStorage.setItem('MeteorToys_shell', value)

		if (value === "") {
			MeteorToys.set("Shell_Clearable", false);
		} else { 
			MeteorToys.set("Shell_Clearable", true);
		}
	}
})

Template.MeteorToys_shell_input.rendered = function () {
	thing = localStorage.getItem('MeteorToys_shell') || "";
	$("#MeteorToys_shell_input").val(thing)

	if (thing === "") {
		MeteorToys.set("Shell_Clearable", false);
	} else { 
		MeteorToys.set("Shell_Clearable", true);
	}
};

Thing = Package["meteortoys:toykit"].ToyKit;
Tracker.autorun(function () {
	if (Thing.equals("current","MeteorToys_shell")) {
		window.setTimeout(function() {
			$("#MeteorToys_shell_input").focus();
		}, 300)
	}
})